import * as React from 'react';
//import 'bootstrap/dist/css/bootstrap.min.css'
//import { Container,Row, Col } from 'react-bootstrap';
//import {MarsCalendar} from '../shared/MarsCalendar';
//import rd3 from 'react-d3-library'
import * as d3 from "d3";
import { useD3 } from '../shared/useD3hook';

export const D3Component:React.FunctionComponent<{debug:boolean; data:any}> = (props) => {
//d3.version
    const ref = useD3(
        (svg) => {
          const height = 500;
          const width = 500;
          const margin = { top: 20, right: 30, bottom: 30, left: 40 };
    
          const x = d3
            .scaleBand()
            .domain(props.data.map((d) => d.year))
            .rangeRound([margin.left, width - margin.right])
            .paddingInner(0.1);
    
          const y1 = d3
            .scaleLinear()
            .domain([0, d3.max(props.data, (d) => d.sales as any)] as any)
            .rangeRound([height - margin.bottom, margin.top]);
    
          const xAxis = (g) =>
            g.attr("transform", `translate(0,${height - margin.bottom})`).call(
              d3
                .axisBottom(x)
                .tickValues(
                  d3
                    .ticks(...d3.extent(x.domain()), width / 40)
                    .filter((v) => x(v) !== undefined)
                )
                .tickSizeOuter(0)
            );
    
          const y1Axis = (g) =>
            g
              .attr("transform", `translate(${margin.left},0)`)
              .style("color", "steelblue")
              .call(d3.axisLeft(y1).ticks(null, "s"))
              .call((g) => g.select(".domain").remove())
              .call((g) =>
                g
                  .append("text")
                  .attr("x", -margin.left)
                  .attr("y", 10)
                  .attr("fill", "currentColor")
                  .attr("text-anchor", "start")
                  .text(props.data.y1)
              );
    
          svg.select(".x-axis").call(xAxis);
          svg.select(".y-axis").call(y1Axis);
    
          svg
            .select(".plot-area")
            .attr("fill", "steelblue")
            .selectAll(".bar")
            .data(props.data)
            .join("rect")
            .attr("class", "bar")
            .attr("x", (d) => x(d.year))
            .attr("width", x.bandwidth())
            .attr("y", (d) => y1(d.sales))
            .attr("height", (d) => y1(0) - y1(d.sales));
        },
        [props.data.length]
      );

    return  <svg
                ref={ref}
                    style={{
                    height: 500,
                    width: "100%",
                    marginRight: "0px",
                    marginLeft: "0px",
                }}
            >
    <g className="plot-area" />
    <g className="x-axis" />
    <g className="y-axis" />
  </svg>;
};
